
* {
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}

body {
  padding: 60px
}
input{
  color: gray;
  font-size: 1.2 rem;
  width: 100%;
}

.card{
  align-content: center;
  border: 1px solid grey;
  border-radius: 6px;
  margin-bottom: 10px;
  margin-right: 10px;
  overflow: hidden;
}


.card p{
  padding: 5px;
  text-align: center;
}

.card img{
  margin: 0 auto;
  width: 150px;
  max-height: 100px;
}

.card-grid {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}